<template>
<div>
  <el-button-group class="mb-2">
    <el-button size="mini" plain icon="icon icon-plus" />
    <el-button size="mini" plain icon="icon icon-spinner11" />
  </el-button-group>
  <el-table
    :data="tableData"
    row-key="id"
    size="small"
    border
    default-expand-all>
    <el-table-column
      prop="date"
      label="Alias"
      sortable>
    </el-table-column>
    <el-table-column
      prop="name"
      label="Long name"
      sortable>
    </el-table-column>
    <el-table-column
      fixed="right"
      width="100">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="addRow(scope.$index, tableData)"
          type="text"
          size="small"
          class="success--text"
          icon="icon icon-plus">
        </el-button>
        <el-button
          @click.native.prevent="addRow(scope.$index, tableData)"
          type="text"
          size="small"
          icon="icon icon-pencil">
        </el-button>
        <el-button
          @click.native.prevent="deleteRow(scope.$index, tableData)"
          type="text"
          size="small"
          class="danger--text"
          icon="icon icon-bin2">
        </el-button>
      </template>
    </el-table-column>
  </el-table>
</div>
</template>
<script>
export default {
  data() {
    return {
      tableData: [
        {
          id: 1,
          date: 'Collection',
          name: 'Quản lý thu hồi nợ'
        }, 
        {
          id: 2,
          date: 'BBR',
          name: 'Thẩm định khách hàng doanh nghiệp'
        }, 
        {
          id: 3,
          date: 'DPS',
          name: 'Quản trị dữ liệu, quy trình hoạt động và công nghệ tin học',
          children: [
            {
              id: 31,
              date: 'DATA',
              name: 'Dữ liệu'
            }, 
            {
              id: 32,
              date: 'PROCESS',
              name: 'Quy trình'
            },
            {
              id: 33,
              date: 'SYSTEM',
              name: 'Hệ thống'
            }
          ]
        }
      ]
    }
  }
}
</script>


