<template>
  <div>
    <el-row :gutter="20" class="mb-2">
      <el-col :span="6">
        <el-button-group>
          <el-button :disabled="loading" size="mini" plain icon="icon icon-plus" />
          <el-button :disabled="loading" size="mini" plain icon="icon icon-spinner11" @click="getUsers" />
        </el-button-group>
      </el-col>
      <el-col :offset="14" :span="4">
        <el-input v-model="q.search_key" placeholder="Search" size="mini" clearable @keyup.native="getUsers">
          <i slot="prefix" class="el-input__icon el-icon-search"></i>
        </el-input>
      </el-col>
    </el-row>
    <el-table
      :data="users.items"
      v-loading="loading"
      element-loading-text="Loading..."
      size="mini"
      border
      style="width: 100%">
      <el-table-column type="index" width="35" align="center" fixed="left" />
      <el-table-column
        v-for="(c, i) in columnDefs"
        :key="i"
        :label="c.label"
        :width="c.width"
        :fixed="c.fixed"
        :prop="c.prop"
        :sortable="true">
        <template slot-scope="scope">
          <div v-if="['roles'].includes(c.prop)">
            <el-tag 
              v-for="(r, i) in scope.row[c.prop]" 
              :key="i"
              type="primary"
              class="mr-1"
              size="small"
              effect="plain">
              <b>{{ r }}</b>
            </el-tag>
          </div>
          <span v-else-if="c.prop === 'created_at'">
            {{ scope.row[c.prop] | moment('from', 'now') || '-' }}
          </span>
          <span v-else-if="c.prop === 'is_active'">
            <el-tag 
              :type="scope.row[c.prop] ? 'success' : 'danger'"
              size="small"
              effect="plain">
              <b>{{ scope.row[c.prop] ? 'Active' : 'Deactive' }}</b>
            </el-tag>
          </span>
          <span v-else>{{ scope.row[c.prop] || '-' }}</span>
        </template>
      </el-table-column>
      <el-table-column
        fixed="right"
        width="70">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="editUser(scope.row)"
            type="text"
            size="small"
            icon="icon icon-pencil">
          </el-button>
          <el-button
            @click.native.prevent="deleteUser(scope.$index, users.items)"
            type="text"
            size="small"
            class="danger--text"
            icon="icon icon-bin2">
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      v-if="users.total > 0"
      :disabled="loading"
      :total="users.total"
      :page-sizes="[20, 50, 100]"
      :page-size="q.page_size"
      :current-page="q.page_number"
      class="right mt-2"
      layout="total, sizes, prev, pager, next, jumper"
      background
      @current-change="currentPageChange"
      @size-change="sizeChange">
    </el-pagination>
  </div>
</template>

<script>
export default {
  created() {
    this.getUsers()
  },

  data() {
    return {
      q: {
        search_key: null,
        page_size: 20,
        page_number: 1
      },
      loading: false,
      users: {},
      columnDefs: [
        { label: 'Username', prop: 'username', width: 120 },
        { label: 'Fullname', prop: 'fullname' },
        { label: 'Email', prop: 'email' },
        // { label: 'Mail name', prop: 'mail_name' },
        { label: 'Department', prop: 'department' },
        { label: 'Sub Department', prop: 'sub_department' },
        { label: 'Title', prop: 'title' },
        { label: 'Roles', prop: 'roles' },
        { label: 'Joined at', prop: 'created_at', width: 130 },
        { label: 'Status', prop: 'is_active', width: 100 }
      ]
    }
  },

  methods: {
    editUser(user) {
      console.log(user)
    },

    currentPageChange(val) {
      this.page_number = val
      this.getUsers()
    },

    sizeChange(val) {
      this.page_size = val
      this.getUsers()
    },

    getUsers() {
      this.loading = true
      this.$services.users.get({ params: this.q })
        .then(res => this.users = res.data)
        .finally(_ => this.loading = false)
    }
  }
}
</script>



