<template>
  <el-tabs type="card">
    <el-tab-pane v-if="checkPermission(['superuser'])" label="Users">
      <users />
    </el-tab-pane>
    <el-tab-pane label="Department">
      <department />
    </el-tab-pane>
    <el-tab-pane label="Assign">
      <assign />
    </el-tab-pane>
  </el-tabs>
</template>

<script>
import checkPermission from '@/utils/permission'
import Assign from '@/components/Settings/Assign'
import Users from '@/components/Settings/Users'
import Department from '@/components/Settings/Department'
export default {
  name: 'Settings',

  components: {
    Assign,
    Users,
    Department
  },

  methods: {
    checkPermission
  }
}
</script>
<style scoped lang="css">
  >>>.el-tabs__item {
    font-weight: bold;
  }
  .wrapper > .main > .content > .inner {
    margin: 1.25rem 0;
  }
  >>>.el-tabs__content {
    margin: 0 1rem;
  }
  >>>.el-tabs__nav {
    margin-left: 1rem;
  }
</style>



